.slider {
  position: relative;
  margin-top: 32px;
  width: 100%;

  @include media-tablet {
    margin-top: 12px;
    margin-bottom: 67px;
  }

  @include media-desktop {
    margin-top: 33px;
  }
}

.slider__button-wrapper {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.slider__button {
  @include basic-text-fs16-lh20;
  @include reset-style-button;
  font-family: $main-font;
  background-color: transparent;
  border-bottom: 1px dotted $slider-btn-dots-border;

  @include media-tablet {
    @include text-fs20-lh30;
  }
}

.slider__button:hover {
  position: relative;
  opacity: 0.6;
  border-bottom: 1px solid $slider-btn-dots-border;
}

.slider__button:active {
  border-bottom: none;
  opacity: 0.3;
}

.slider__button--before {
  margin-left: 38px;

  @include media-tablet {
    margin-left: 63px;
  }

  @include media-desktop {
    margin-left: 73px;
  }
}

.slider__button--after {
  margin-left: auto;
  margin-right: 33px;

  @include media-tablet {
    margin-right: 62px;
  }

  @include media-desktop {
    margin-right: 23px;
  }
}

.slider__input {
  position: absolute;
  top: 45%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 84px;
  height: 14px;
  background: $primary-bg-color;
  border: 1px solid $border-color;
  border-radius: 6px;

  @include media-tablet {
    width: 428px;
    height: 6px;
    background-color: $slider-bar-bg;
  }

  @include media-desktop {
    top: 50%;
    transform: translate(-45%, -50%);
  }
}

.slider__range [type="range"] {
  -webkit-appearance: none;
  top: 45%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 84px;
  height: 14px;
  background: $primary-bg-color;
  border: 1px solid $border-color;
  border-radius: 6px;

  @include media-tablet {
    width: 428px;
    height: 6px;
    background-color: $slider-bar-bg;
  }

  @include media-desktop {
    top: 50%;
    transform: translate(-45%, -50%);
  }
}

/* custom thumb */
.slider__range [type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 35px;
  height: 6px;
  top: 3px;
  left: 6px;
  background: $basic-green;
  border-radius: 6px;
  border: none;
  cursor: pointer;

  @include media-tablet {
    width: 34px;
    height: 34px;
    border-radius: 50%;
    border: $primary-bg-color 12px solid;
    top: 4px;
    left: 50%;
    background-color: $basic-green;
  }
}

.slider__range [type="range"]::-webkit-slider-thumb:hover {
  box-shadow: $slider-box-shadow;
}

.slider__range [type="range"]::-moz-range-thumb:active {
  border: 1px solid $button-color;
  box-shadow: $slider-box-shadow;
}

.slider__range [type="range"]::-moz-range-thumb:focus {
  outline: none;
  border: 1px solid $button-color;
  box-shadow: $slider-box-shadow;
}


